import { apiRequest } from "./api";
import React, { createContext, useEffect, useState } from "react";

//! :  .
// 1.  .
// 2.  App.js -     DataProvider,    
// 3.    medicalHistory.js (    ""  ).        - const { doctorArray } = useContext(DataContext);
// -     services.js
export const DataContext = createContext();

export const DataProvider = ({ children }) => {
  const [servicesArray, setServicesArray] = useState([]);
  const [doctorArray, setDoctorArray] = useState([]);

  useEffect(() => {
    apiRequest("departments", "GET").then((data) => {
      setServicesArray(data);
    });
  }, []);

  useEffect(() => {
    apiRequest("doctors", "GET").then((data) => {
      setDoctorArray(data);
    });
  }, []);

  return (
    <DataContext.Provider value={{ servicesArray, doctorArray }}>
      {children}
    </DataContext.Provider>
  );
};
